@import './css/reset.css';
@import './css/jumbotron.css';
@import './css/detail.css';
@import './css/cart.css';

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.alert {
  width: 80%;
  height: 10vh;
  margin: 0;
  background-color: rgb(248, 225, 95);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.shoe-title {
  font-size: 24px;
}

.btn-primary {
  margin: 40px 0;
}